<template>
  <div class="register-mask" v-if="config.isShow">
    <div class="register-box" @click.stop>
      <img class="register-dialog" src="@/assets/images/reg/reg-bg.png" />
      <div class="register-info">
        <div class="title">您已获得王者荣耀皮肤，请注册</div>
        <div class="title" style="margin-left:5px;">安装大家乐棋牌APP，立即领取！</div>
        <div class="mobile-input">
          <img src="@/assets/images/reg/mobile-bg.png" />
          <input v-model="formData.mobile" type="number" placeholder="请输入您的手机号" />
        </div>
        <div class="code-box">
          <div class="code-input">
            <img src="@/assets/images/reg/code-bg.png" />
            <input v-model="formData.code" type="text" placeholder="请输入验证码" />
          </div>
          <img class="code-btn" src="@/assets/images/reg/code-btn.jpg" @click="getCheckCode" />
        </div>
        <div class="register-tips">温馨提示：游戏登录密码默认手机后6位</div>
        <img class="submit" src="@/assets/images/reg/submit.jpg" @click="submit" />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["config"],
  data() {
    return {
      formData: {
        mobile: null,
        code: null
      }
    };
  },
  mounted() {
    this.$eventBus.$on("showRegister", () => (this.config.isShow = true));
  },
  methods: {
    getCheckCode() {
      if (this.formRule()) {
        //获取验证码
        this.$msg.success("获取验证码");
      }
    },
    submit() {
      if (this.formRule("submit")) {
        //提交
        this.$msg.success("提交成功");
        this.config.isShow = false;
      }
    },
    formRule(type = "default") {
      let state = 1;
      let mobileReg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
      let { mobile, code } = this.formData;

      if (!mobile) {
        this.$msg.fail("请输入手机号");
        state = 0;
      } else if (!mobileReg.test(mobile)) {
        this.$msg.fail("请输入正确的手机号");
        state = 0;
      } else if (type == "submit" && !code) {
        this.$msg.fail("请输入验证码");
        state = 0;
      } else if (type == "submit" && code) {
        this.$msg.fail("校验验证码");
        state = 1;
      }
      return state;
    }
  }
};
</script>
<style scoped lang="stylus">
input {
  width: 100%;
  background: none;
  font-size: 15px;
  position: absolute;
  left: 0;
  top: -3px;
  height: 100%;
  color: #fff;

  &::placeholder {
    color: #ddd;
  }
}

.register-mask {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
}

.register-box {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 306px;

  .register-dialog {
    width: 100%;
    display: block;
    margin: 0 auto;
  }

  .register-info {
    width: 100%;
    position: absolute;
    left: 0;
    top: 35px;

    .title {
      width: 100%;
      padding: 0 40px;
      font-size: 15px;
      text-align: center;
      color: #f8d15b;
    }

    .mobile-input {
      width: 100%;
      padding: 0 20px;
      margin-top: 5px;

      img {
        width: 100%;
      }

      input {
        padding: 0 20px 0 30px;
      }
    }

    .code-box {
      width: 100%;
      padding: 0 20px;
      margin-top: 5px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .code-input {
        flex: 1;
        margin-right: 15px;

        img {
          width: 100%;
        }

        input {
          padding: 0 10px;
        }
      }

      .code-btn {
        min-width: calc((176 / 2));
        width: calc((176px / 2));
      }
    }

    .register-tips {
      color: red;
      font-size: 12px;
      text-align: center;
      width: 100%;
    }

    .submit {
      width: 113px;
      display: block;
      margin: 5px auto 0;
    }
  }
}
</style>

