<template>
  <div class="lottery">
    <img class="wheel" src="@/assets/images/lottery_img.png" />
    <img
      class="pointer"
      :style="{'transform':styleTransform,'transition':'transform 6s ease-in-out'}"
      src="@/assets/images/pointer.png"
      @click="startLottery"
    />
  </div>
</template>
<script>
export default {
  data() {
    return {
      styleTransform: "rotate(0deg)", //初始旋转角度\
      isDisabled: false,
      resultIndex: 0, //初始抽奖结果 , 0-11 抽奖结果
      rotateStartAngle: 0
    };
  },
  mounted() {},
  methods: {
    startLottery() {
      if (!this.isDisabled) {
        this.getLottery();
      }
    },
    getLottery() {
      this.is_disabled = true;
      let fullCircleCount = 10 * 360; //转几圈

      //初始旋转角度 每次抽奖重新注册
      let rotateAngle =
        this.rotateStartAngle +
        fullCircleCount +
        30 * this.resultIndex -
        (this.rotateStartAngle % 360); //设置当前旋转的角度  转圈数 + 当前结果的角度 - 上次旋转的角度
      this.styleTransform = "rotate(" + rotateAngle + "deg)"; //给指针赋值 当前旋转的角度
      this.rotateStartAngle = rotateAngle; // 设置下次旋转的起始度数
      setTimeout(() => {
        this.endLottery();
      }, 6000);
    },
    endLottery() {
      //   this.$eventBus.$emit("showRegister");
    }
  }
};
</script>
<style scoped lang="stylus">
.lottery {
  position: relative;
  top: -30px;
  width: 100%;

  .wheel {
    display: block;
    margin: 0 auto;
    width: 331px;
  }

  .pointer {
    position: absolute;
    left: 31%;
    top: 19%;
    width: 141px;
    transform-origin: 48% 60%;
  }
}
</style>

